@import url('https://fonts.googleapis.com/css2?family=Alexandria:wght@400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .gradient-bg {
    @apply bg-gradient-to-br from-gradient1 via-gradient2 to-gradient3;
  }
  
  .gradient-text {
    @apply bg-gradient-to-r from-gradient1 to-gradient2 bg-clip-text text-transparent;
  }
  
  .gradient-text-2 {
    @apply bg-gradient-to-r from-gradient1 to-gradient3 bg-clip-text text-transparent;
  }
  
  .gradient-purple {
    @apply bg-gradient-to-r from-purple-500 to-blue-600 bg-clip-text text-transparent;
  }
  
  .phone-mockup {
    @apply bg-gradient-to-br from-gray-300 to-gray-800 rounded-[52px] relative;
  }
  
  .phone-screen {
    @apply relative overflow-hidden transition-transform duration-300;
  }
  
  .phone-screen:hover img {
    @apply scale-105;
  }
  
  .screenshot-item {
    @apply transition-all duration-300;
  }
  
  .screenshot-item:hover {
    @apply -translate-y-2;
  }
}
